.services{
    @include grid(1, 0);    
    @include tablet{
        grid-template-columns: repeat(2, 1fr);
    }
}
.service{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 60rem;
    padding: 5rem 1.5rem;
    @include tablet{
        max-height: 60rem;              
    }
    @include desktop{
        padding-left: 16rem;
        padding-right: 16rem;
    }    
}
.no-webp .graphic-design{
    background-image: url('../img/mobile/image-graphic-design.jpg');
    @include tablet {
        background-image: url('../img/desktop/image-graphic-design.jpg');
    }
}
.webp .graphic-design{
    background-image: url('../img/mobile/image-graphic-design.webp');
    @include tablet {
        background-image: url('../img/desktop/image-graphic-design.webp');
    }
}
.no-webp .photography{
    background-image: url('../img/mobile/image-photography.jpg');
    @include tablet {
        background-image: url('../img/desktop/image-photography.jpg');
    }
}
.webp .photography{
    background-image: url('../img/mobile/image-photography.webp');
    @include tablet {
        background-image: url('../img/desktop/image-photography.webp');
    }
}
.service__title, .service__text{
    text-align:center; 
    
}
.service__title{
    font-size: 3rem;
}
.service__text{
    margin-top: 2rem;
    font-size: 1.6rem;
    @include desktop{
        font-size: 1.8rem;
    }
}
.graphic-design{
    color:$darkDesaturatedCyan;
}
.photography{
    color: $darkBlue;
}