  line-height: 1.5; }

.no-webp .hero {
  background-image: url("../img/mobile/image-header.jpg"); }
  @media (min-width: 768px) {
    .no-webp .hero {
      background-image: url("../img/desktop/image-header.jpg"); } }

.webp .hero {
  background-image: url("../img/mobile/image-header.webp"); }
  @media (min-width: 768px) {
    .webp .hero {
      background-image: url("../img/desktop/image-header.webp"); } }

.hero {
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
  height: 54rem; }
  @media (min-width: 768px) {
    .hero {
      height: 80rem; } }

.hero-content__title {
  color: white;
  font-weight: 900;
  font-size: clamp(3rem, 8vw, 5.4rem);
  line-height: 1.2;
  text-transform: uppercase;
  letter-spacing: .8rem;
  margin: 7rem 0; }
  @media (min-width: 998px) {
    .hero-content__title {
      letter-spacing: 1.2rem;
      margin: 10rem 0 7rem 0; } }

.hero-content__arrow {
  height: 12rem;
  text-align: center;
  animation: saltar .6s infinite ease-in-out alternate; }
  .hero-content__arrow img {
    max-height: 100%;
    width: auto; }

@keyframes saltar {
  from {
    transform: translateY(0); }
  to {
    transform: translateY(1rem); } }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
  position: relative; }
  @media (min-width: 998px) {
    .header {
      padding: 4rem .5rem; } }

.header__logo a {
  display: flex; }

.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32rem;
  width: 100%;
  padding: 3rem;
  background-color: white;
  position: absolute;
  top: 7.5rem;
  left: 0;
  text-align: center;
  clip-path: polygon(100% 0, 100% 0, 100% 0, 100% 0, 100% 0);
  z-index: 1; }
  @media (min-width: 998px) {
    .navigation {
      justify-content: flex-end;
      height: auto;
      padding: 0;
      position: relative;
      top: 0;
      clip-path: none;
      background-color: transparent;
      padding: 0;
      top: 0;
      bottom: 0; } }
  .navigation ul {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-top: 2rem; }
    @media (min-width: 998px) {
      .navigation ul {
        flex-direction: row;
        padding: 0; } }
  .navigation.show {
    display: block;
    clip-path: polygon(95% 10%, 100% 0%, 100% 100%, 0 100%, 0 10%); }

.navigation--link a {
  display: inline-block;
  min-width: 14rem;
  border-radius: 4rem;
  padding: 1.5rem 0;
  color: #818498;
  font-weight: 600;
  transition: all .4s ease-in-out; }
  @media (min-width: 998px) {
    .navigation--link a {
      color: white;
      font-weight: 400; } }
  .navigation--link a:hover {
    font-family: "Fraunces", serif;
    text-transform: uppercase;
    font-weight: 600;
    background-color: #fbd601;
    color: #23303e; }

@media (min-width: 998px) {
  .navigation--link:hover a {
    background-color: #6eceff; } }

.burguer-menu {
  cursor: pointer; }
  @media (min-width: 998px) {
    .burguer-menu {
      display: none; } }
  .burguer-menu .fas {
    color: white;
    font-size: 2.5rem; }
  .burguer-menu:hover .fas {
    color: #fbd601; }
  .burguer-menu.active .fas {
    color: #fbd601; }

.blocks {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr); }
  @media (min-width: 768px) {
    .blocks {
      grid-template-columns: repeat(2, 1fr); } }
  .blocks .block {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    width: 100%;
    height: 31.2rem; }
    @media (min-width: 768px) {
