.content-block {
  padding: 5rem 2rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white; }
  @media (min-width: 998px) {
    .content-block {
      justify-content: center;
      padding: 0 7rem;
      text-align: left; } }
  @media (min-width: 1440px) {
    .content-block {
      padding: 0 11rem; } }

.content-block__title {
  font-size: 3.8rem;
  max-width: 42rem; }

.content-block__text {
  margin: 3rem 0;
  color: #818498; }

.content-block__link {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: #23303e;
  position: relative;
  z-index: 1; }
  .content-block__link::after {
    content: '';
