.content-block__link--softYellow::after {
  background-color: #fbd5014b; }

.content-block__link--softYellow:hover::after {
  background-color: #fbd601; }

.content-block__link--softRed::after {
  background-color: #fe796767; }

.content-block__link--softRed:hover::after {
  background-color: #fe7867; }

@media (min-width: 768px) {
