.no-webp .hero{
    background-image: url('../img/mobile/image-header.jpg');
    @include tablet{
        background-image: url('../img/desktop/image-header.jpg');
    }
}
.webp .hero{
    background-image: url('../img/mobile/image-header.webp');
    @include tablet{
        background-image: url('../img/desktop/image-header.webp');
    }
}
.hero{
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    height: 54rem;    
    @include tablet{
        height: 80rem;
    }
}
.hero-content__title{
    color: $white;
    font-weight: $bolder;
    font-size: clamp(3rem, 8vw, 5.4rem);
    line-height: 1.2;
    text-transform: uppercase;
    letter-spacing: .8rem;
    margin: 7rem 0;
    @include desktop{
        letter-spacing: 1.2rem;
        margin: 10rem 0 7rem 0;
    }
}
.hero-content__arrow{
    height: 12rem;
    text-align: center;
    animation: saltar .6s infinite ease-in-out alternate;
    img{
        max-height: 100%;
        width: auto;
    }
}
@keyframes saltar {
    from{
        transform: translateY(0);
    }
    to{
        transform: translateY(1rem);
    }
    
}