html{
    font-size: 62.5%;
    font-family: $barlow;
    box-sizing: border-box;
    color: $DarkGrayishBlue;
}
*, *:before, *:after {
    box-sizing: inherit;
    transition: all .3s ease-in-out;
  }
  body{      
      font-size: 1.8rem;
      font-weight: $semi;
      background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
      background-attachment: fixed;
      overflow-y: scroll;
      scroll-behavior: smooth;
  }

  img{
      max-width: 100%;
  }
  ul {
      padding: 0;
      margin: 0;
      list-style: none;
  }
  a {
      text-decoration: none;
  }
  h1, h2, h3, h4 {
      font-family: $fraunces;
      color: $darkDesaturatedBlue;
      margin: 0;
  }  
  h2{
      font-size: 3rem;
  }
  h3{
      font-size: 2.6rem;
  }
  p{
      margin: 0;
      line-height: 1.5;
  }