.footer{
    background-color: $footer;
    color: $darkModerateCyan;
    padding: 4rem 1.5rem 6rem 1.5rem;
    text-align: center;
    display: flex;
    flex-direction: column;    
}
.footer__logo{
    path{
            fill: $darkModerateCyan;
        }
    margin-bottom: 2rem;           
}
.footer__navigation{
    ul{
        display: flex;
        justify-content: space-between;
        width: 30rem;
        margin: 0 auto;        
    }    
}
.footer__navigation--link{
    a{
        color: $darkModerateCyan;
        &:hover{
            color: $white;
        }
    }    
}
.footer__social{
    margin-top: 4rem;    
    ul{
        li{
            display: inline-block;
            font-size: 2.5rem;
            margin-right: 1.5rem;
            cursor: pointer;            
            &:last-of-type{
                margin: 0;
            }
        }
    }
    .fab:hover{
        color: $white;
    }
}