.client-testimonials{
    padding: 8rem 0;
    h3{
        color: $grayishBlue;
        text-align: center;
        margin-bottom: 8rem;
        text-transform: uppercase;
    }
}
.testimonials{
    @include tablet{
        @include grid(3, 2rem)
    }
}
.testimonial{
    padding: 3rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.testimonial__avatar{
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto ;
}
.testimonial__text{
    text-align: center;
    line-height: 2;
    margin-top: 4rem;
    flex: 1;
}
.testimonial__name{
    margin-top: 2rem;
    font-size: 2.4rem;
}
.testimonial__position{
    font-size: 1.5rem;
    margin-top: 1.5rem;
    color: $grayishBlue;
}