.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
    position: relative;
    @include desktop{
        padding: 4rem .5rem;
    }
}
.header__logo{
    a{
        display: flex;
    }
}
.navigation{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32rem;
    width: 100%;
    padding: 3rem;
    background-color: $white;    
    position: absolute;
    top: 7.5rem;
    left: 0;    
    text-align: center;
    clip-path: polygon(100% 0, 100% 0, 100% 0, 100% 0, 100% 0);
    z-index: 1;
    @include desktop{        
        justify-content: flex-end;
        height: auto;
        padding: 0;
        position: relative;
        top: 0;
        clip-path: none;
        background-color: transparent;
        padding: 0;
        top: 0;
        bottom: 0;
    }
    ul {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        padding-top: 2rem;       
        @include desktop{            
            flex-direction: row;
            padding: 0;
        }
    }
    &.show{
        display: block;
        clip-path: polygon(95% 10%, 100% 0%, 100% 100%, 0 100%, 0 10%);
    }
}
.navigation--link{
    a{
        display: inline-block;
        min-width: 14rem;
        border-radius: 4rem;
        padding: 1.5rem 0;
        color: $DarkGrayishBlue;
        font-weight: $semi;
        transition: all .4s ease-in-out;
       @include desktop{
           color:$white;
           font-weight: $regular;

       }
       &:hover{
        font-family: $fraunces; 
        text-transform: uppercase;
        font-weight: $semi;
        background-color: $linkHoverYellow;
        color: $darkDesaturatedBlue;
       }               
    }
    @include desktop{
        &:hover a{      
            background-color: $linkHoverBlue;   
        }
    }
}
.burguer-menu{    
    cursor: pointer;
    @include desktop{
        display: none;
    }
    .fas{
        color: $white;
        font-size: 2.5rem;
    }
    &:hover{
        .fas{
            color: $linkHoverYellow;
        }
    }
    &.active{
        .fas{
            color: $linkHoverYellow;
        }
        
    }
}