.blocks{
    @include grid(1, 0);
    @include tablet{
        grid-template-columns: repeat(2, 1fr);
    }
    .block{
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 100%;
        height: 31.2rem;
        @include tablet{
            height: 40rem;
        }
        @include desktop{
            height: 60rem;
        }
    }    
}
.no-webp .blocks{
    .eggBlock{
        background-image: url('../img/mobile/image-transform.jpg');
        @include tablet{
            background-image: url('../img/desktop/image-transform.jpg');
        }
    }
    .cupBlock{
        background-image: url('../img/mobile/image-stand-out.jpg');
        @include tablet{
            background-image: url('../img/desktop/image-stand-out.jpg');
        }
    }
}
.webp .blocks{
    .eggBlock{
        background-image: url('../img/mobile/image-transform.webp');
        @include tablet{
            background-image: url('../img/desktop/image-transform.webp');
        }
    }
    .cupBlock{
        background-image: url('../img/mobile/image-stand-out.webp');
        @include tablet{
            background-image: url('../img/desktop/image-stand-out.webp');
        }
    }
}
.content-block{
    padding: 5rem 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $white;
    @include desktop{        
        justify-content: center;
        padding: 0 7rem;
        text-align: left;
    }
    @include hd{
        padding: 0 11rem;
    }
    
}
.content-block__title{
    font-size: 3.8rem;
    max-width: 42rem;
}
.content-block__text{
    margin: 3rem 0;
    color: $DarkGrayishBlue;
}
.content-block__link{
    text-transform: uppercase;
    font-family: $fraunces;
    font-weight: $bolder;
    color: $darkDesaturatedBlue;
    position: relative;
    z-index: 1;   
    &::after{
        content: '';
        position:absolute;
        top: 1.3rem;
        left: -.4rem;
        right: 0;
        width: 13.8rem;
        height: 1.4rem;
        border-radius: .7rem;
        z-index: -1;
    }    
}
.content-block__link--softYellow{
    &::after{
        background-color:#fbd5014b;
    }
    &:hover::after{
        background-color: $linkHoverYellow;
    }
}
.content-block__link--softRed{
    &::after{
        background-color:#fe796767;        
        
    }
    &:hover::after{
        background-color: $softRed;
    }
}
.eggBlock{
    @include tablet{
        grid-column: 2/3;
    }
}
.content-block-brand{
    @include tablet{
        grid-row: 1/2;
    }
}